import React,{useState} from 'react';
import classes from './ProductPage.module.css';
import Menu from '../../Menu/Menu';
import Footer from '../../Footer/Footer';
import {useSelector} from 'react-redux';

const ProductPage = (props) => {
    const data = useSelector(state => state.slider.data.dataArr);
    const [prize,setPrize] = useState(data.prize);
    const [displayImg,setDisplayImg] = useState(data.url);
    let isMobile = false;
    if(window.innerWidth <= 765){
        isMobile = true;
    }

    const selectPrize = (type) => {
        switch(type) {
            case 'false':
                return setPrize(Number(data.prize) + 5);
            default:
                setPrize(data.prize);
        }
    };
    return (
        <>
            <Menu/>
            <div className={classes.Conteiner}>
                <section className={classes.LeftSide}>
                    <img src={displayImg} alt='Product Poto'></img>
                    <div>{
                       data.url && data.url.map(el => <img className={classes.MiniImg} onClick={()=> setDisplayImg(el)} src={el} key={el} alt='Product Poto'></img>)
                    }</div>
                    {!isMobile && <div className={classes.Description} >{data.description}</div>}
                </section>
                <section className={classes.RightSide}>
                    <h1 className={classes.Title}>{data.name}</h1>
                    {isMobile &&  <div className={classes.Description} >{data.description}</div>}
                    <div style={{paddingTop:'23px',fontSize:'18px'}}>z {prize}</div>
                    <p>Czyszczenie</p>
                    <select className={classes.SelectBox} onChange={(event)=>selectPrize(event.target.value)}>
                        <option value={true}>oddam produkt wyczyszczcony</option>
                        <option value={false}>oddam produkt bez czyszczenia</option>
                    </select>
                    <p>TRANSPORT</p>
                    <ul style={{borderBottom:'1px solid black',paddingBottom:'20px'}}>
                        <li>Odbir osobisty w naszej Pracowni</li>
                    </ul>
                    <p>REGULAMIN</p>
                    <ul style={{borderBottom:'1px solid black',paddingBottom:'20px'}}>
                        <li>prcz opaty za wypoyczenie pobierana jest kaucja zwrotna</li>
                        <li>wypoyczalnia dziaa na terenie caej Polski  na Pastwa yczenie wysyamy asortyment w dowolne miejsce</li>
                        <li>przy rezerwacji asortymentu podpisujemy umow, ktra jest gwarancj terminu (umow otrzymuj Pastwo mailem w cigu 3 dni od rezerwacji)</li>
                        <li>po podpisaniu umowy prosimy o wpacenie zadatku w kwocie zgodniej z zapisem w umowie</li>
                        <li>pozosta naleno + kaucj zwrotn wpacaj Pastwo przed wysyk</li>
                        <li>kaucja zwracana jest przy zwrocie towaru</li>
                        <li>wysyk otrzymuj Pastwo w czwartek lub pitek do godz. 12:00 poprzedzajcy uroczysto</li>
                        <li>zwrot asortymentu do wtorku po uroczystoci, ewentualnie do rody po wczeniejszym uzgodnieniu</li>
                        <li>koszty wysyki i zwrotu le po stronie klienta</li>
                        <li>moliwy odbir osobisty w Nowym Sczu</li>
                        <li>asortyment otrzymuj Pastwo czysty, wyprasowany  przygotowany do uycia</li>
                        <li>zwracany towar musi by w stanie niezmienionym (czyste, wyprane, wyprasowane)</li>
                        <li>ewentualne zniszczenia pokrywane s z kaucji</li>
                        <li>istnieje moliwo zwrotu niewyczyszczonych produktw, doliczana jest wwczas opata za czyszczenie/pranie: 10 z/szt</li>
                        <li>pamitaj, aby oznaczy opcj czyszczenia</li>
                    </ul>
                </section>
            </div>
            <Footer/>
        </>
    );
};

export default ProductPage;